<template>
  <v-dialog v-model="$store.state.form.closed" max-width="400px">
    <template v-slot:activator="{on, attrs}">
      <span v-bind="attrs" v-on="on" class="d-flex align-center px-5 text-decoration-none white--text">Регистрация</span>
    </template>
    <v-card>
      <v-card-title>
        <span class="headline">Регистрация</span>
      </v-card-title>
      <v-card-text>
        <v-form>
          <v-container>
            <v-row>
              <v-col>
                <v-text-field
                v-model="email"
                :error-messages="emailErrors"
                label="E-mail"
                required
                @input="$v.email.$touch()"
                @blur="$v.email.$touch()"
                ></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col>
                <v-text-field
                v-model="password"
                type="password"
                :error-messages="passwordErrors"
                label="Пароль"
                required
                @input="$v.password.$touch()"
                @blur="$v.password.$touch()"
                ></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col>
                <v-text-field
                v-model="name"
                :error-messages="nameErrors"
                label="Имя"
                required
                @input="$v.name.$touch()"
                @blur="$v.name.$touch()"
                ></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col>
                <small>{{ $store.state.registration.regError }}</small>
              </v-col>
            </v-row>
          </v-container>
        </v-form>
      </v-card-text>
      <v-card-text>
        <span id="toAuth" @click="toAuth">Авторизоваться</span>
      </v-card-text>
      <v-card-actions class="justify-end">
        <v-btn @click="submit" text color="primary">Зарегистрироваться</v-btn>
        <v-btn text color="primary" @click="close">Закрыть</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import {required, email} from 'vuelidate/lib/validators'

export default {
  name: 'Register',
  data: function() {
    return {
      email: '',
      password: '',
      name: ''
    }
  },
  validations: {
    email: {
      required,
      email
    },
    password: {
      required
    },
    name: {
      required
    }
  },
  computed: {
    emailErrors() {
      const errors = []
      if (!this.$v.email.$dirty) return errors
      !this.$v.email.required && errors.push('Необходимо ввести e-mail!')
      !this.$v.email.email && errors.push('Должен быть действующий e-mail!')
      return errors
    },
    passwordErrors() {
      const errors = []
      if (!this.$v.password.$dirty) return errors
      !this.$v.password.required && errors.push('Необходимо ввести пароль!')
      return errors
    },
    nameErrors() {
      const errors = []
      if (!this.$v.name.$dirty) return errors
      !this.$v.name.required && errors.push('Введите имя!')
      return errors
    },
  },
  methods: {
    toAuth() {
      this.$store.commit('formSwitch', 'auth')
    },
    close() {
      this.$store.commit('closer')
    },
    async submit() {
      let userData = {
        email: this.email,
        password: this.password,
        name: this.name
      }
      await this.$store.dispatch('register', userData)
    }
  }
}
</script>

<style scoped>
  small {
    color: red;
    text-align: center;
  }
  #toAuth {
    text-decoration: underline;
    color: #1E88E5;
    cursor: pointer;
  }
</style>