<template>
  <div id="grid">
    <v-card 
      v-for="item of desktopProp" 
      :key="item.id"
      class="card d-flex flex-column justify-space-between orange lighten-4"
      width="300px">
      <div>
        <v-img
          position="top"
          width="300px"
          height="260px"
          :src="require('../assets/images/' + item.img)">
        </v-img>
        <v-card-title class="py-1">{{ item.name }}</v-card-title>
        <v-card-text class="py-0 text-justify">{{ item.description }}</v-card-text>
      </div>
      <v-card-actions class="align-self-end">
        <div id="price">{{ item.price + ' ₽' }}</div>
        <v-btn 
          bottom text 
          color="blue" 
          @click="toCart(item)">В корзину
        </v-btn>
      </v-card-actions>
    </v-card>
  </div>
</template>

<script>
export default {
  name: 'Desktop',
  props: ['desktopProp'],
  methods: {
    toCart(item) {
      this.$store.commit('addToCart', item)
    }
  }
}
</script>

<style>
  #grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    justify-items: center;
  }
  .card {
    margin: 1.111vw;
  }
  #price {
    margin-right: 130px;
  }
</style>