<template>
  <div>
    <v-dialog v-model="$store.state.cart.cartIsClosed" max-width="600px">
      <template v-slot:activator="{on, attrs}">
        <v-icon 
          v-bind="attrs" 
          v-on="on" 
          class="white--text">mdi-cart-outline</v-icon>
      </template>
      <v-card>
        <v-card-title class="blue--text">Корзина</v-card-title>
        <v-simple-table>
          <template>
            <thead>
              <tr>
                <th>Название</th>
                <th>Количество</th>
                <th>Стоимость</th>
                <th>Удалить</th>
              </tr>
            </thead>
            <tbody v-if="$store.state.cart.cart.length < 1">
              <tr>
                <td colspan="4" class="text-center">Корзина пуста!</td>
              </tr>
            </tbody>
            <tbody v-else>
              <tr v-for="item in $store.state.cart.cart" :key="item.id">
                <td>{{ item.name }}</td>
                <td width="20px" class="">
                  <v-text-field type="number" :rules="[rules.required, rules.counter]" v-model="item.quantity"></v-text-field>
                </td>
                <td>{{ item.price * item.quantity }}</td>
                <td>
                  <v-btn text fab small color="blue" @click="$store.commit('delete', item)">
                    <v-icon>mdi-cart-off</v-icon>
                  </v-btn>
                </td>
              </tr>
              <tr>
                <td>Итого:</td>
                <td></td>
                <td>{{ finalSum }}</td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>
        <v-card-text>
          <small v-if="$store.state.cart.notAuthorized" class="red--text">Для оформления заказа необходимо авторизоваться!</small>
          <small v-if="$store.state.cart.orderIsDone" class="green--text">Заказ оформлен!</small>
          <small v-if="$store.state.cart.emptyCart" class="red--text">Выберите товар!</small>
        </v-card-text>
        <v-card-actions class="d-flex justify-end">
          <v-btn small @click="makeOrder" color="primary">Оформить заказ</v-btn>
          <v-btn small @click="$store.commit('cartCloser')" color="error">Закрыть</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>

export default {
  name: 'Cart',
  data: () => ({
    rules: {
      required: value => !!value || 'Ведите количество',
      counter: value => value >= 1 || 'Не менее 1'
    }
  }),
  computed: {
    finalSum() {
      let arr = this.$store.state.cart.cart;
      let sum = 0;
      arr.forEach(element => {
        sum = sum + (element.price * element.quantity)
      });
      return sum
    }
  },
  methods: {
    makeOrder() {
      if (this.$store.state.authorization.user == 'Гость') {
        this.$store.commit('notAuthorizedChanger');
        return
      }
      if (this.$store.state.authorization.user != 'Гость' && this.$store.state.cart.cart.length == 0) {
        this.$store.commit('emptyCartChanger')
        return
      }
      else {
        this.$store.commit('orderIsDoneChanger');
        this.$store.dispatch('getCurrentUser', this.$store.state.cart.cart);
      }
    }
  }
}
</script>