<template>
  <v-dialog v-model="$store.state.form.closed" max-width="400px">
    <template v-slot:activator="{on, attrs}">
      <span v-bind="attrs" v-on="on" class="d-flex align-center px-5 text-decoration-none white--text">Авторизация</span>
    </template>
    <v-card>
      <v-card-title>
        <span class="headline">Авторизация</span>
      </v-card-title>
      <v-card-text>
        <v-form>
          <v-container>
            <v-row>
              <v-col>
                <v-text-field
                v-model="email"
                :error-messages="emailErrors"
                label="E-mail"
                required
                @input="$v.email.$touch()"
                @blur="$v.email.$touch()"
                ></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col>
                <v-text-field
                v-model="password"
                type="password"
                :error-messages="passwordErrors"
                label="Пароль"
                required
                @input="$v.password.$touch()"
                @blur="$v.password.$touch()"
                ></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col>
                <small>{{ $store.state.authorization.authError }}</small>
              </v-col>
            </v-row>
          </v-container>
        </v-form>
      </v-card-text>
      <v-card-text>
        Не зарегистрированы?
        <span id="toReg" @click="toReg">Регистрация</span>
      </v-card-text>
      <v-card-actions class="justify-end">
        <v-btn @click="submit" text color="primary">Авторизоваться</v-btn>
        <v-btn text color="primary" @click="close">Закрыть</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import {required, email} from 'vuelidate/lib/validators'

export default {
  name: 'Auth',
  data: function() {
    return {
      email: '',
      password: ''
    }
  },
  validations: {
    email: {
      required,
      email
    },
    password: {
      required
    }
  },
  computed: {
    emailErrors: function() {
      const errors = []
      if (!this.$v.email.$dirty) return errors
      !this.$v.email.required && errors.push('Требуется e-mail!')
      !this.$v.email.email && errors.push('Должен быть действующий e-mail!')
      return errors
    },
    passwordErrors: function() {
      const errors = []
      if (!this.$v.password.$dirty) return errors
      !this.$v.password.required && errors.push('Необходимо ввести пароль!')
      return errors
    },
  },
  methods: {
    toReg: function() {
      this.$store.commit('formSwitch', 'Register')
    },
    close: function() {
      this.$store.commit('closer')
    },
    submit: function() {
      let userData = {
        email: this.email,
        password: this.password
      }
      this.$store.dispatch('changeUser', userData)
    }
  }
}
</script>

<style scoped>
  small {
    color: red;
    text-align: center;
  }
  #toReg {
    text-decoration: underline;
    color: #1E88E5;
    cursor: pointer;
  }
</style>