<template>
    <v-app-bar app class="blue lighten-1">
      <v-container fluid>
        <v-row class="d-flex justify-space-between">
          <v-col>
            <router-link to="/" class="logo">iFlower</router-link>
          </v-col>
          <v-col class="d-flex justify-end align-center">
            <span class="white--text">{{ username }}</span>
            <component id="logout" v-bind:is="regAuth"></component>
            <Cart></Cart>
          </v-col>
        </v-row>
      </v-container>
    </v-app-bar>
</template>

<script>
import Auth from './auth'
import Register from './register'
import Logout from './logout'
import Cart from './cart'

export default {
  name: 'Navbar',
  computed: {
    username: function() {
      return this.$store.state.authorization.user
    },
    regAuth: function() {
      return this.$store.state.form.authOrRegister
    }
  },
  components: {
    Auth, Register, Logout, Cart
  }
}
</script>

<style lang="css" scoped>
  @import url('https://fonts.googleapis.com/css2?family=Shadows+Into+Light&display=swap');

    .logo {
      font-family: 'Shadows Into Light';
      font-size: 2vw;
      text-decoration: none;
      color: white;
    }
    #logout {
      cursor: pointer;
    }

  @media (max-width: 600px) {
    .logo {
      font-size: 5vw;
    }
  }
</style>