<template>
  <div>
  <v-carousel
    show-arrows
    light
    hide-delimiters
    height="500px">
    <v-carousel-item
      v-for="(item, id) of mobileProp" :key="id">
      <v-card
        width="300px"
        class="card mx-auto">
        <v-img
          position="top"
          height="260px"
          width="300px"
          :src="require('../assets/images/' + item.img)">
        </v-img>
        <div class="card-text-wrapper d-flex flex-column justify-space-between orange lighten-4">
          <div>
            <v-card-title class="py-1">{{ item.name }}</v-card-title>
            <v-card-text class="py-0 text-justify">{{ item.description }}</v-card-text>
          </div>
          <v-card-actions class="align-self-end">
            <div id="price">{{ item.price + ' ₽' }}</div>
            <v-btn 
              bottom 
              text color="blue"
              @click="toCart(item)">В корзину</v-btn>
          </v-card-actions>
        </div>
      </v-card>
    </v-carousel-item>
  </v-carousel>
  </div>
</template>

<script>
export default {
  name: 'Mobile',
  props: ['mobileProp'],
  methods: {
    toCart(item) {
      this.$store.commit('addToCart', item)
    }
  }
}
</script>

<style>
  .card-text-wrapper {
    height: 202px;
  }
  #price {
    margin-right: 130px;
  }
</style>