<template>
  <v-app>
    <Navbar></Navbar>
    <v-main app id="mp-background">
      <div>  
        <template v-if="show">
          <Desktop :desktopProp="items"></Desktop>
          <v-pagination
            v-model="currentPage"
            :length="pages"
            total-visible="3"
            @input="changePage">
          </v-pagination>
        </template>
        <Mobile v-else :mobileProp="allItems"></Mobile>
      </div>
    </v-main>
  </v-app>
</template>

<script>
import _ from 'lodash'
import Navbar from '@/components/navbar'
import Desktop from '@/components/desktop'
import Mobile from '@/components/mobile'
import data from '@/mixins/data.mixin'

export default {
  name: 'Mainpage',
  mixins: [data],
  data: () => ({
    items: [],
    desktopItems: [],
    pageSize: 8,
    currentPage: 1,
    pages: 0,
    show: true
  }),
  methods: {
    setupPagination() {
      this.desktopItems = this.allItems;
      this.desktopItems = _.chunk(this.desktopItems, this.pageSize);
      this.pages = _.size(this.desktopItems);
      this.items = this.desktopItems[this.currentPage - 1] || this.desktopItems[0];
    },
    changePage(currentPage) {
      this.items = this.desktopItems[currentPage - 1] || this.desktopItems[0];
    },
    clientWidth() {
      let windowSize = document.documentElement.clientWidth;
      if (windowSize >= 650) {
        this.show = true;
      } else {
        this.show = false;
      }
    }
  },
  created() {
    window.addEventListener('resize', this.clientWidth);
    this.clientWidth();
  },
  mounted () {
    this.setupPagination();
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.clientWidth)
  },
  components: {
    Desktop, Mobile, Navbar
  }
}
</script>

<style>
  #mp-background {
    background-color: #BBDEFB;
    background-image: url('../assets/springtime.png');
    background-position: right;
    background-size: auto 100%;
  }
</style>